@font-face {
  font-family: "font-2";
  src: url(../../assets/fonts/BlackDahlia.ttf) format("truetype");
}

$font-2: "font-2", sans-serif;

.navigation {

  height: 280px;
  background-image: url('../../assets/img/empyrrhia typo flo.png');
  //background-size:contain;
  width: auto;
  background-position: center;
  background-repeat: no-repeat;
  //background-size: cover;
  position: relative;
  overflow: hidden;
  min-width: min-content;


  ul {
    //font-family: $font-2;
    font-weight: bold;
    display: flex;
    position: absolute;
    bottom: 0;
    text-align: center;

    a {
      // backdrop-filter: blur(2px) invert(90%);
      // background-color: rgba(128, 128, 128, 0.281);
      width: 160px;
      text-decoration: none;
      padding: 20px 30px 10px 30px;
      white-space: nowrap;
      font-size: 32px;
      // width: fit-content;
      width: 150px;
      text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.185),
        -2px 2px 1px rgba(0, 0, 0, 0.452);
      z-index: 0;
      transition: opacity 0.5s ease-in-out;
      -moz-transition: opacity 0.5s ease-in-out;
      -webkit-transition: opacity 0.5s ease-in-out;

      &:hover {
        // background-image: radial-gradient(rgba(141, 141, 141, 0.507), rgba(255, 255, 255, 0.171), rgba(255, 255, 255, 0));

        opacity: 100%;
        transition: opacity 1s ease-in-out;
        -moz-transition: opacity 1s ease-in-out;
        -webkit-transition: opacity 1s ease-in-out;
      }

      li {
        display: inline;
        left: 50%;
        overflow: hidden;
        color: rgb(214, 214, 214);
        transition: 0.2s;
        cursor: pointer;
        padding-top: 4px;
        border-top: 4px solid rgba(214, 214, 214, 0);
        text-decoration: none rgba(214, 214, 214, 0);
        text-underline-offset: 6px;

        &:hover {
          text-decoration: underline rgba(214, 214, 214, 1) 4px;
          border-top: 4px solid rgba(214, 214, 214, 1);
          transition: text-decoration 0.5s ease-out, border-top 0.5s ease-out;
          -moz-transition: text-decoration 0.5s ease-out, border-top 0.5s ease-out;
          -webkit-transition: text-decoration 0.5s ease-out, border-top 0.5s ease-out;
        }
      }
    }

  }
}

.nav-active {
  position: relative;

  &::after {

    content: "";
    height: 100%;
    width: 0;
    // background-color: rgba(0, 0, 0, 0.950);
    backdrop-filter: blur(1px) invert(100%);
    position: absolute;
    bottom: 0px;
    left: 49.9%;
    transform: translateX(-50%);
    -webkit-animation: anim 0.3s ease forwards;
    animation: anim 0.6s ease forwards;

    @-webkit-keyframes anim {
      to {
        width: 100%;
      }
    }

    @keyframes anim {
      to {
        width: 100%;
      }
    }
  }

  &:hover {
    opacity: 100%;
  }

  // &::after li {
  //   text-decoration: underline rgb(0, 0, 0) 4px;
  //   border-top: 4px solid rgba(214, 214, 214, 1);
  // }

}


@media (min-width: 300px) and (max-width: 995px) {
  .navigation {
    height: 120px;
    width: 100%;
    background-size: cover;

    ul {
      position: left;
      display: inline-flex;
      flex: content;


      a {
        font-size: 14px;
        padding: 15px 2% 13px 2%;
        width: 95px;
      }
    }
  }
}

@media (min-width: 750px) and (max-width:995px) {

  a {
    width: 110px;
  }
}